body {
  background-color: #222;
  color: #ccc;
  font-size: larger;
}

main {
  text-align: justify;
  padding: 10px 8%;
  border-width: 2px 0 2px 0;
  border-style: solid;
  border-image: linear-gradient(to right, rgba(0,0,0,0) 5%, #ee2233, #eebb33, #22bb33, #2299ee, rgba(0, 0, 0, 0) 95%) 1;
}

.App {
  text-align: center;
}

header {
  padding: 20px;
}

nav {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-evenly;
  background: linear-gradient(to right, #ee2233 10%, #eebb33, #22bb33, #2299ee 90%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  color: transparent;
  padding-top: 10px;
}

h1 {
  margin: 0;
  background: radial-gradient(#fff, rgba(0,0,0,0));
  background-clip: text;
  -webkit-text-fill-color: transparent;
  color: transparent;
}

h2 {
  text-align: center;
  background: radial-gradient(#fff, rgba(0,0,0,0));
  background-clip: text;
  -webkit-text-fill-color: transparent;
  color: transparent;
}

h3 {
  margin: 0;
  padding: 0;
}

.title {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-evenly;
  align-items: center;
  margin: 12px auto;
}

.left-title {
  text-align: right;
  padding-right: 15px;
  width: 30%;
}

.center-title {
  border-width: 1px;
  border-style: solid;
  border-image: linear-gradient(to bottom, rgba(0,0,0,0), #fff, rgba(0,0,0,0)) 1 100%;
  padding: 0 5px;
  text-align: center;
  width: 30%;
}

.right-title {
  text-align: left;
  padding-left: 15px;
  width: 30%;
}

ul {
  list-style-type: none;
  background: linear-gradient(to right bottom, #ee2233 15%, #eebb33, #22bb33, #2299ee 75%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  color: transparent;
  font-size: x-large;
  display: grid;
  display: grid;
  grid-template-columns: repeat(auto-fill, 130px);
  justify-content: space-evenly;
  padding-left: 20px;
  margin: 20px auto;
}

a {
  text-decoration: none;
}

a:visited {
  color: inherit;
}

.no-gradient {
  background: #ccc;
  background-clip: text;
  -webkit-text-fill-color: #ccc;
  color: #ccc;
  padding-left: 2px;
  font-size: large;
}

img {
  width: 100%;
  border-width: 1px;
  border-style: solid;
  border-image: radial-gradient(rgba(0,0,0,0), #fff, rgba(0,0,0,0)) 1;
}

form {
  display: flex;
  flex-flow: column nowrap;
  padding-bottom: 20px;
  width: fit-content;
  margin: 0 auto;
}

label {
  padding: 0 0 10px 0;
}

input {
  width: 250px;
  height: 20px;
  padding: 2px;
  border-color: #fff;
  border-width: 2px;
  border-style: inset;
  border-radius: 3px;
  margin-bottom: 15px;
}

textarea {
  width: 250px;
  height: 80px;
  padding: 2px;
  border-color: #fff;
  border-width: 2px;
  border-style: inset;
  border-radius: 3px;
}

button {
  width: 80px;
  height: 25px;
  align-self: center;
  margin-top: 20px;
  border-color: #fff;
  border-width: 2px;
  border-style: outset;
  border-radius: 3px;
}

button:active {
  border-style: inset;
}

.icons {
  font-size: xx-large;
  margin-top: 8px;
}

.icon {
  margin-left: 15px;
}

iframe {
  margin-top: 20px;
  margin-bottom: 20px;
}

footer {
  padding: 20px;
  font-size: smaller;
}

@media (max-width: 420px) {
  body {
    font-size: unset;
  }
}
